import Vue from 'vue'
import Router from 'vue-router'
import {createAsyncRouter} from '@/utils/index'
import {getStorage} from "@/utils";
import store from '@/store'

Vue.use(Router);

/**
 * 基础路由
 * @type {*[]}
 */
const baseRouter = [
    {
        path: '/',
        redirect: '/login',
    },
    {
        path: '/login',
        name: 'login',
        component: () => import('@/views/login.vue')
    },
    {
        path: '/404',
        name: 'page404',
        component: () => import('@/views/page404.vue')
    },
    {
        path: '/401',
        name: 'page401',
        component: () => import('@/views/page401.vue')
    }
];

/**
 * 实例化router
 * @type {VueRouter}
 */
const router = new Router({
    routes: baseRouter
});

/**
 * 页面刷新后，根据userInfo重新加载路由
 * @type {any}
 */
const userInfo = JSON.parse(getStorage('userInfo'));
if (userInfo) {
    const accessKeys = userInfo['accessKeys'];
    if (accessKeys.length) {
        let asyncRouter = createAsyncRouter(userInfo['accessKeys']);
        router.addRoutes(asyncRouter);
        store.commit('SET_ASYNCROUTER',asyncRouter);
    }
}

/**
 * 设置路由拦截
 */
router.beforeEach((to, from, next) => {
    const userInfo = JSON.parse(getStorage('userInfo'));
    if(to.name === 'login'){
        if(!!userInfo){
            let asyncRouter = store.state.user.asyncRouter;
            next( asyncRouter[0].path );
        }else{
            next();
        }
    }else{
        if(!!userInfo){
            next();
        } else {
            next('/login');
        }
    }
});

export default router;
