import Vue from 'vue'
import Vuex from 'vuex'
import {setStorage,getStorage} from '../utils/index'


Vue.use(Vuex);
// 用户相关
const userModule = {
    state: {
        userInfo: JSON.parse(getStorage('userInfo')),
        asyncRouter: []
    },
    getters: {
        userInfo: state => {
            return state.userInfo
        },
        asyncRouter: state => {
            return state.asyncRouter
        }
    },
    mutations: {
        ['SET_USERINFO'](state, userInfo) {
            state.userInfo = userInfo;
            /**
             * 保存store成功，存入session
             */
            setStorage('userInfo',JSON.stringify(userInfo))
        },
        ['SET_ASYNCROUTER'](state, asyncRouter) {
            state.asyncRouter = asyncRouter;
        }
    }
};

// 其他业务相关
const otherModule = {
    state: {
        roleList: []
    },
    getters: {
        roleList: state => {
            return state.roleList
        }
    },
    mutations: {
        ['SET_ROLELIST'](state, roleList) {
            state.roleList = roleList;
        }
    }
};


export default new Vuex.Store({
    modules: {
        user: userModule,
        other: otherModule
    }
});

