import {Message} from 'element-ui';
import {$SERVICE_CODE_ERR, $SYS_ERR, $SERVICE_ERR} from '@/config/dictionary'

/**
 * 对axios返回的数据进行if判断
 * @param res
 * @param success
 * @param reject
 */
export function logic(res, success, reject) {
    if (res.data.status === 1) {
        /**
         * 请求成功，返回数据
         */
        success(res.data.result);
    } else if (res.data.status === 0) {
        /**
         * 请求被拒绝，返回错误信息
         */
        reject(res.data.message);
    } else {
        Message({
            type: 'error',
            message: $SERVICE_CODE_ERR
        });
    }
}

/**
 * 处理访问请求过程中的语法错误，及服务错误
 * @param error
 */
export function handleError(error) {
    let msg = '';
    if ('response' in error) {
        msg = $SERVICE_ERR
    } else {
        msg = $SYS_ERR
    }
    Message({
        type: 'error',
        message: msg
    });
    throw Error(error);
}

/**
 * 过滤发送请求时的无效参数
 * @param params
 */
export function paramsFilter(params) {
    let obj = {};
    for (let key in params) {
        if (!!params[key] && JSON.stringify(params[key]) !== '{}' && JSON.stringify(params[key]) !== '[]') {
            obj[key] = params[key];
        }
    }
    return obj;
}

/**
 * 通过用户权限列表添加路由
 * @param accessKeys
 */
export function createAsyncRouter(accessKeys) {
    const routes = [];
    /********************************************
     * path 、component、title 等信息均由后台返回
     {
        path: '/userManage',
        component: layout,
        meta: {icon: 'user', title: '用户管理', index: '1'},
        children: [
            {
                path: '',
                name: 'userList',
                component: () => import('@/views/userManage/userList.vue'),
                meta: {title: '用户列表', index: '1-1'}
            }
        ]
    }
     **********************************************/
    let iconMap = {
        '用户管理': 'user',
        '商户管理': 'shop',
        '商品列表': 'foods',
        '角色管理': 'role',
        '资金流水': 'stream',
    };

    accessKeys.forEach((item, index) => {
        let route = null;
        if (!item.children || !item.children.length) {
            // 如果没有子菜单
            route = {
                path: item.path,
                component: () => import('@/views/layout/index.vue'),
                children: [{
                    path: '',
                    component: () => import('@/views/' + item.component + '/index.vue'),
                    name: item.component,
                    meta: {
                        icon: iconMap[item.title],
                        title: item.title,
                        authority: item.authority,
                        index: index.toString()
                    }
                }]
            }
        } else {
            // 如果存在子菜单
            if (item.children.length > 1) {
                route = {
                    path: item.path,
                    component: () => import('@/views/' + item.component + '/index.vue'),
                    meta: {icon: iconMap[item.title], title: item.title, index: index.toString()},
                    children: (() => {
                        let routerChildren = [];
                        item.children.forEach((child, i) => {
                            routerChildren.push({
                                path: i === 0 ? '' : child.path,
                                component: () => import('@/views' + item.path + '/' + child.component + '.vue'),
                                name: child.component,
                                meta: {title: child.title, authority: child.authority, index: index + '-' + i},
                            });
                        });
                        return routerChildren;
                    })()
                };
            } else {
                route = {
                    path: item.path,
                    component: () => import('@/views/layout/index.vue'),
                    children: [{
                        path: '',
                        component: () => import('@/views' + item.path + '/' + item.children[0].component + '.vue'),
                        name: item.children[0].component,
                        meta: {
                            icon: iconMap[item.title],
                            title: item.children[0].title,
                            authority: item.children[0].authority,
                            index: index.toString()
                        }
                    }]
                }
            }

        }
        routes.push(route);
    });
    return routes;
}

/**
 * 转义select列表状态码
 * @param arr
 * @param val
 * @returns {string}
 */
export function getVauleLabel(arr, val) {
    let label = '';
    arr.forEach(function (e, index) {
        if (val == e.value) {
            label = e.label;
        }
    });
    return label;
}

/**
 * translator 获取select code码对应的label
 * @param code
 * @param arr
 * @returns {string}
 */
export function translator(code, arr) {
    return getVauleLabel(arr, code);
}

/**
 * 表单数据映射
 */
export function dataMapping(form, data) {
    if(!!data){
        let obj = Object.assign({},data);
        for (let key in form) {
            form[key] = obj[key] || form[key];
        }
    }
}

/**
 * 重置form表单
 * @param form
 */
export function resetFormData(form) {
    for (let key in form) {
        if (typeof form[key] === 'string') {
            form[key] = '';
            return;
        }
        if (typeof form[key] === 'number') {
            form[key] = 0;
            return;
        }
        if (typeof form[key] === 'boolean') {
            form[key] = false;
            return;
        }
        if (form[key].constructor === 'Array') {
            form[key] = [];
            return;
        }
        if (form[key].constructor === 'Object') {
            form[key] = {};
            return;
        }
        if (form[key] === undefined) {
            form[key] = undefined;
            return;
        }
    }
}

/**
 * 选中日期
 * @param fromDay
 * @param toDay
 * @returns {*}
 */
export function selectDate(fromDay, toDay) {
    // 参数fromDay（从距离今天第几天开始）
    // 参数toDay（截止到距离今天第几天结束）
    if (toDay > -1) {
        let toDate = new Date().getTime() - (3600 * 1000 * 24 * toDay);
        let fromDate = toDate - (3600 * 1000 * 24 * fromDay);
        return [formatDate('yyyy-MM-dd', fromDate), formatDate('yyyy-MM-dd', toDate)];
    } else {
        let fromDate = new Date().getTime() - (3600 * 1000 * 24 * fromDay);
        return formatDate('yyyy-MM-dd', fromDate);
    }
}

/**
 * 日期格式化
 * @param fmt
 * @param timestamp
 * @returns {*}
 */
export function formatDate(fmt, timestamp) {
    if (!!timestamp) {
        let date = new Date(timestamp);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        let o = {
            "M+": date.getMonth() + 1,                 //月份
            "d+": date.getDate(),                    //日
            "h+": date.getHours(),                   //小时
            "m+": date.getMinutes(),                 //分
            "s+": date.getSeconds(),                 //秒
            "q+": Math.floor((date.getMonth() + 3) / 3), //季度
            "S": date.getMilliseconds()             //毫秒
        };
        if (/(y+)/.test(fmt))
            fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (let k in o)
            if (new RegExp("(" + k + ")").test(fmt))
                fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return fmt;
    }
}

// SET sessionStorage
export function setStorage(key, value) {
    return sessionStorage.setItem(key, value);
}

/**
 * GET sessionStorage
 * @param key
 * @returns {string}
 */
export function getStorage(key) {
    return sessionStorage.getItem(key);
}

/**
 * DEL sessionStorage
 * @param key
 */
export function delStorage(key) {
    sessionStorage.removeItem(key);
}

/**
 * el是否包含class类
 * @param el
 * @param cls
 * @returns {boolean}
 */
export function hasClass(el, cls) {
    if (!el || !cls) return false;
    if (cls.indexOf(' ') !== -1) throw new Error('className should not contain space.');
    if (el.classList) {
        return el.classList.contains(cls);
    } else {
        return (' ' + el.className + ' ').indexOf(' ' + cls + ' ') > -1;
    }
};

/**
 * el增加class类
 * @param el
 * @param cls
 */
export function addClass(el, cls) {
    if (!el) return;
    let curClass = el.className;
    let classes = (cls || '').split(' ');

    for (let i = 0, j = classes.length; i < j; i++) {
        let clsName = classes[i];
        if (!clsName) continue;

        if (el.classList) {
            el.classList.add(clsName);
        } else if (!hasClass(el, clsName)) {
            curClass += ' ' + clsName;
        }
    }
    if (!el.classList) {
        el.className = curClass;
    }
};

/**
 * el删除class类
 * @param el
 * @param cls
 */
export function removeClass(el, cls) {
    if (!el || !cls) return;
    let classes = cls.split(' ');
    let curClass = ' ' + el.className + ' ';

    for (let i = 0, j = classes.length; i < j; i++) {
        let clsName = classes[i];
        if (!clsName) continue;

        if (el.classList) {
            el.classList.remove(clsName);
        } else if (hasClass(el, clsName)) {
            curClass = curClass.replace(' ' + clsName + ' ', ' ');
        }
    }
    if (!el.classList) {
        el.className = trim(curClass);
    }
};

/**
 * 窗口改变时，延迟resize方法
 * @param func
 * @param wait
 * @param immediate
 * @returns {function(...[*]=): *}
 */
export function debounce(func, wait, immediate) {

    let timeout, args, context, timestamp, result
    const later = function () {
        // 据上一次触发时间间隔
        const last = +new Date() - timestamp

        // 上次被包装函数被调用时间间隔last小于设定时间间隔wait
        if (last < wait && last > 0) {
            timeout = setTimeout(later, wait - last)
        } else {
            timeout = null
            // 如果设定为immediate===true，因为开始边界已经调用过了此处无需调用
            if (!immediate) {
                result = func.apply(context, args)
                if (!timeout) context = args = null
            }
        }
    }

    return function (...args) {
        context = this
        timestamp = +new Date()
        const callNow = immediate && !timeout
        // 如果延时不存在，重新设定延时
        if (!timeout) timeout = setTimeout(later, wait)
        if (callNow) {
            result = func.apply(context, args)
            context = args = null
        }

        return result
    }
}
